import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Autosuggest from 'react-autosuggest';
import "./Autosuggest.css"

var _ = require('lodash')

const COLORS = {
    SPECIES:"#fb2056",
    GENUS:"#fc8f5b",
    FAMILY:"#ffd055",
    ORDER:"#8dd58c",
    CLASS:"#38c9b1",
    PHYLUM:"#1798c3",
    KINGDOM:"#182573",
  }
  
  const ORDER = {
    KINGDOM:1,
    PHYLUM:2,
    CLASS:3,
    ORDER:4,
    FAMILY:5,
    GENUS:6,
    SPECIES:7,
    SUBSPECIES:8,
    VARIETY:9,
  }
  
  
// Teach Autosuggest how to calculate suggestions for any given input value.
const getSuggestions = async value => {
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;  
    if (inputLength < 3){
      return []
    } else {
        return await getSuggestionsGBIF(value)
        .then(results => {
          return results;
        })        
    }
  };
  
const getSuggestionValue = suggestion => suggestion.name;

// Use your imagination to render suggestions.
const renderSuggestion = suggestion => (
    <div>
      {suggestion.rank === undefined ?
       "" :
       <span style={{backgroundColor:COLORS[suggestion.rank] || "gray",fontSize:12,width:80, borderRadius:'15%',padding:'2px'}}>{suggestion.rank}</span> //
      } {suggestion.name}
    </div>
  );
  
const getSuggestionsGBIF = async (q) => {
    const response = await fetch(`https://api.gbif.org/v1/species/suggest?q=${encodeURI(q)}`)
    var json = await response.json()
    var results = []
    if (json.length===0) {
      const res = await fetch(`https://api.gbif.org/v1/species/search?q=${encodeURI(q)}`)
      json = await res.json()
      console.log(json)
      json['results'].forEach(el => {
        if ('canonicalName' in el) {
          results.push(
            {
              name:el.canonicalName,
              rank:el.rank,
              order:ORDER[el.rank] || 8
            }            
          )
        }
      });
    } else {
      results = json.map(el => ({
        name:el.canonicalName,
        rank:el.rank,
        order:ORDER[el.rank] || 8
      }))
    }

    results = _.sortBy(results,"order")    
    return results
  }

export default class GbifAutosuggest extends Component {
    constructor (props) {
        super(props)
        this.state = {
          value: '',
          suggestions: []
        }
      }
             
    onChange = (event, { newValue }) => {
        this.setState({value: newValue});
      };
            
    // Autosuggest will call this function every time you need to update suggestions.
    // You already implemented this logic above, so just use it.
    onSuggestionsFetchRequested = ({ value }) => {
        getSuggestions(value).then(results => {
          this.setState({
            suggestions: results
          });
        })    
      };

    // Autosuggest will call this function every time you need to clear suggestions.
    onSuggestionsClearRequested = () => {
        this.setState({
          suggestions: []
        });
      };

    onSuggestionSelected = (event, {suggestion, suggestionValue, suggestionIndex, sectionIndex, method }) => {
        this.props.setProps({value:suggestion}) //jQuery.param( suggestion)
      };      

    render() {
        const inputProps = {
            placeholder: 'Search for a species',
            value: this.state.value,
            onChange: this.onChange,      
          };
        const { value, suggestions } = this.state;
        return (
            <div id={this.props.id}>
                <Autosuggest
                    suggestions={suggestions}
                    onSuggestionSelected = {this.onSuggestionSelected}
                    onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                    onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                    getSuggestionValue={getSuggestionValue}
                    renderSuggestion={renderSuggestion}
                    multiSection={false}
                    inputProps={inputProps}
                />            
            </div>
        )
    }
}

GbifAutosuggest.defaultProps = {};

GbifAutosuggest.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * A label that will be printed when this component is rendered.
     */
    label: PropTypes.string.isRequired,

    /**
     * The value displayed in the input.
     */
    value: PropTypes.any,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
